  const [message, setMessage] = useState("");
  const [errorInput, setErrorInput] = useState("");
  const [sentEmail, setSentEmail] = useState(false);

  function validateEmail(mail) {
    if (/^\w+([\.-]?\w+)*@\w+([\.-]?\w+)*(\.\w{2,3})+$/.test(mail)) {
      return true;
    }
    return false;
  }

  async function sendEmail() {
    if (!name) {
      setErrorInput("name");
      console.log("fill name");
      return "false";
    } else if (!phone) {
      setErrorInput("phone");
      console.log("fill phone");
      return "false";
    } else if (!email || !validateEmail(email)) {
      setErrorInput("email");
      console.log("fill email");
      return "false";
    } else if (!company) {
      setErrorInput("company");
      console.log("fill company");
      return "false";
    } else if (!position) {
      setErrorInput("position");
      console.log("fill position");
      return "false";
    } else if (!message) {
      setErrorInput("message");
      console.log("fill message");
      return "false";
    }

    const url = `https://consiliumintelligence.com/contact/podsemailServer.php`;
    console.log(`register twilio token`);
    try {
      const reqObj = {
        method: "POST",
        credentials: "same-origin",
        body: JSON.stringify({
          name: name,
          company: company,
          phone: phone,
          email: email,
          position: position,
          message: message
        })
      };
      const result = await fetch(url, reqObj);
      const response = await result.json();
      if (response.sent) {
        return setSentEmail(true);
      } else if (!response.sent) {
        return setSentEmail(false);
      }
      return response;
    } catch (err) {
      console.log(err);
    }
    return null;
  }

